.img-component {
    width: 27rem;
    height: auto;
    overflow: hidden;
    filter: drop-shadow(14px 10px 4px $color-metal-bronze);

    @include respond(tiny-phone) {
        width: 32rem;
    }


    @include respond(small-phone) {
        width: 42rem;
    }


    @include respond(tablet) {
        width: 60.2rem;

        &--different {
            width: 50.2rem;
        }
    }


    @include respond(huge-desktop) {
        max-width: 73rem;

        &--different {
            width: 51.2rem;
        }
    }


    &__img {
        width: 100%;
        object-fit: cover;
        transition: all .3s ease-in-out;

        &:hover {
            transform: scale(1.02);
            cursor: pointer;
        }
    }
}
