.managers {
    padding-bottom: 2rem;

    @include respond(small-phone) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }


    @include respond(tablet) {
        padding-bottom: 8rem;
    }


    @include respond(small-desktop) {
        padding-bottom: 10.4rem;
    }


    &__body {
        @include flex(column-reverse, center, center);

        @include respond(small-phone) {
            @include flex(row, flex-start, space-between);
        }
    }

    &__img {
        @include respond(small-phone) {
            margin-right: 1.7rem;
        }
    }

    &__details {}

    &__info-item {
        @include flex(row, center, flex-start);

        cursor: pointer;
        margin-bottom: 1.3rem;

        &:last-child {
            margin-bottom: 2rem;
        }

        @include respond(small-phone) {
            margin-bottom: 2rem;

            &:last-child {
                margin-bottom: 0;
            }
        }


        @include respond(tablet) {
            margin-bottom: 3rem;
        }
    }

    &__info-item:hover &__info-icon-box {
        background-color: transparent;
        border: 1px solid $color-light-blue;
    }

    &__info-item:hover &__info-icon {
        fill: $color-black-magic;
    }

    &__info-icon-box {
        display: none;
        width: 3.5rem;
        height: 3.5rem;
        background-color: $color-orange-peel;
        border-radius: 13.8542px;
        border: 1px solid transparent;
        transition: all .3s ease-in-out;

        &:hover {
            border: 1px solid $color-light-blue;
        }

        @media (min-width: 53em) {
            @include flex(row, center, center);

            margin-right: 1.5rem;
        }

        @include respond(tablet) {
            margin-right: 3rem;
        }
    }

    &__info-icon {
        width: 1.2rem;
        height: 1.6rem;
        fill: $color-white;
        transition: all .3s ease-in-out;
    }

    &__info-info {
        @include adaptiv-font(16, 13);

        font-weight: 500;
        line-height: 2rem;
        color: $color-metal-bronze;

        @include respond(small-phone) {
            max-width: 37rem;
        }


        @include respond(tablet) {
            line-height: 2.6rem;
        }
    }
}
