.section-details {
    &__introduction {
        @include adaptiv-font(16, 14);

        font-weight: 500;
        line-height: 2.2rem;
        color: $color-metal-bronze;
        max-width: 49rem;
        margin-bottom: 1.5rem;

        @include respond(tablet) {
            line-height: 2.6rem;
            margin-bottom: 3rem;
        }
    }

    &__info-item {
        max-width: 49rem;
        margin-bottom: 1.2rem;

        @include respond(small-desktop) {
            max-width: 53rem;
            margin-bottom: 2.5rem;
        }
    }

    &__info-title {
        @include adaptiv-font(16, 14);

        font-weight: bold;
        line-height: 2.6rem;
        color: $color-black-magic;
        margin-bottom: 1.2rem;
    }

    &__info-subtitle {
        @include adaptiv-font(16, 14);

        font-weight: 500;
        line-height: 2.6rem;
        color: $color-metal-bronze;
        margin-bottom: 1.2rem;

        @include respond(small-phone) {
            margin-bottom: 0;
        }


        @include respond(tablet) {
            margin-bottom: 2.5rem;
        }
    }
}
