.marketers {
    padding-bottom: 3rem;

    @include respond(small-phone) {
        padding-bottom: 6rem;
    }


    @include respond(tablet) {
        padding-bottom: 12rem;
    }


    @include respond(small-desktop) {
        padding-bottom: 15.7rem;
    }


    &__body {
        @include flex(column, center, center);

        @include respond(small-phone) {
            @include flex(row, flex-start, space-between);
        }
    }

}
