.features {
    background-image: url(../img/features/bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &__title {
        max-width: 54rem;
        margin: 0 auto 2.3rem;

        @include respond(small-phone) {
            margin-bottom: 3.3rem;
        }


        @include respond(tablet) {
            margin-bottom: 5.4rem;
        }


        @include respond(big-desktop) {
            max-width: 63rem;
        }


        @media (min-width: 120em) {
            max-width: 73rem;
        }

        @media (min-width: 147em) {
            max-width: 83rem;
        }
    }

    &__body {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 1.2rem;
        align-items: flex-start;

        @include respond(small-phone) {
            padding: 0 1.6rem;
            margin-bottom: 2rem;
        }


        @include respond(tablet) {
            padding: 0 1.6rem;
            margin-bottom: 4rem;
        }


        @include respond(small-desktop) {
            padding: 0 1.6rem;
            margin-bottom: 5.4rem;
        }
    }

    &__item {
        flex: 1 0 49%;
        max-width: 49%;
        margin-bottom: 2rem;
        margin-right: .2rem;

        @include flex(column, center, center);

        @include respond(small-desktop) {
            flex: 1 0 25%;
            max-width: 25%;
            margin-bottom: 0rem;
            margin-right: 0rem;

            @include flex(column, flex-start, center);
        }
    }

    &__item-img {
        margin-bottom: 1.5rem;

        @include respond(tablet) {
            margin-bottom: 3rem;
        }
    }

    &__item-detailes {
        text-align: center;

        @include respond(small-desktop) {
            text-align: left;
        }
    }

    &__item-title {
        @include adaptiv-font(22, 17);

        font-weight: 600;
        color: $color-black-magic;
        margin-bottom: 1.3rem;
    }

    &__item-subtitle {
        @include adaptiv-font(16, 15);

        font-weight: 500;
        line-height: 1.6;
        color: $color-metal-bronze;

        @include respond(small-phone) {
            max-width: 24rem;
        }
    }

    &__btn-box {
        @include flex(row, center, center);
    }

    &__btn {
        margin-bottom: 2rem;

        @include respond(small-phone) {
            margin-bottom: 4rem;
        }


        @include respond(tablet) {
            margin-bottom: 6rem;
        }


        @include respond(small-desktop) {
            margin-bottom: 10rem;
        }
    }
}
