.professionals {
    padding-top: 2rem;
    padding-bottom: 2rem;

    @include respond(small-phone) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }


    @include respond(tablet) {
        padding-top: 8rem;
        padding-bottom: 8rem;
    }


    @include respond(small-desktop) {
        padding-top: 11.7rem;
        padding-bottom: 10.4rem;
    }
    
    &__body {
        @include flex(column, center, center);

        @include respond(small-phone) {
            @include flex(row, flex-start, space-between);
        }
    }
}
